import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';

@Component({
  selector: 'cmacs-dashboard-widget-panel',
  templateUrl: './cmacs-dashboard-widget-panel.component.html',
  styleUrls: ['./cmacs-dashboard-widget-panel.component.css']
})
export class CmacsDashboardWidgetPanelComponent implements OnInit {

  @Input() backgroundColor = '#f6f7fb';
  @Input() widgetList: any [] = [];
  @Output() widgetListChange = new EventEmitter<any[]>();

  constructor() { }

  ngOnInit() {
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.widgetList, event.previousIndex, event.currentIndex);
    this.widgetListChange.emit(this.widgetList);
  }

}
