import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { WidgetType, WidgetDataType } from '../core/enums/widget-type.enum';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';

@Component({
  selector: 'cmacs-dashboard-widget-container',
  templateUrl: './cmacs-dashboard-widget-container.component.html',
  styleUrls: ['./cmacs-dashboard-widget-container.component.css']
})
export class CmacsDashboardWidgetContainerComponent implements OnInit {

  @Input() headerText = '';
  @Input() footerText = '';
  @Input() footerValue = '';
  @Input() type = WidgetType.general;
  @Input() dataType = WidgetDataType.single;
  @Input() showChartTypeOption = true;

  WidgetTypeEnum = WidgetActionType;
  WidgetDataTypeEnum = WidgetDataType;
  chartSelected = WidgetActionType.verticalBarChart;
  @Output() menuClick = new EventEmitter<WidgetActionType>();

  constructor() { }

  ngOnInit() {
  }

  onMenuClick(type: WidgetActionType): void {
    if (this.isChartTypeMenu(type)) {
      this.chartSelected = type;
    }
    this.menuClick.emit(type);
  }

  isChartTypeMenu(type: WidgetActionType): boolean {
    return type === WidgetActionType.verticalBarChart || type === WidgetActionType.horizontalBarChart
      || type === WidgetActionType.pieChart || type === WidgetActionType.lineChart || type === WidgetActionType.gauge;
  }
}
